  from {
    height: 0;
  }

  to {
    height: 100%;
  }
}

.Header.white .header__logo {
  color: black;
}

.Header.white::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  z-index: -1;
  -webkit-animation: topbottom 0.2s;
          animation: topbottom 0.2s;
}

.header__container {
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 20px;
  justify-content: space-between;
}

.bars {
  flex: 0.1 1;
  z-index: 30;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.header__bars {
  position: relative;
  width: 40px;
  height: 40px;
  background: black;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header__bars.bars-opened > .header__bar {
  -webkit-transform: translateX(-40px);
          transform: translateX(-40px);
  background: transparent;
}

.header__bars.bars-opened > .header__bar::before {
  -webkit-transform: translate(40px) rotate(45deg);
          transform: translate(40px) rotate(45deg);
  opacity: 1;
}

.header__bars.bars-opened > .header__bar::after {
  opacity: 1;
  -webkit-transform: translate(40px) rotate(-45deg);
          transform: translate(40px) rotate(-45deg);
}

.header__bar {
  width: 30px;
  height: 5px;
  background: white;
  transition: all 0.3s linear;
}

.header__bar::before {
  content: "";
  position: absolute;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  transition: all 0.3s linear;
  width: 30px;
  height: 5px;
  background: white;
}

.header__bar::after {
  content: "";
  transition: all 0.3s linear;

  position: absolute;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
  width: 30px;
  height: 5px;
  background: white;
}

